export default {
	props:{
		list: { // 数据列表
			type: Array,
			default(){
				return []
			}
		}
	},
	methods: {
		copy(e){
			uni.setClipboardData({
				data: e,
				success: function () {
					uni.showToast({
						title: '复制成功',
						icon: 'none'
					});
						// console.log('复制成功');
				}
			});
		}
	},
}
